import React from 'react';
import './Chat.css';
import {auth} from '../index';
// import {useAuthState} from "react-firebase-hooks/auth";
import {useCollectionData} from "react-firebase-hooks/firestore";
import firebase from 'firebase/app';
import 'firebase/firestore';

const Chat = () => {
    let text = '';
    // const {auth, firestore} = useContext(Context)
    // const [user] = useAuthState(auth)
    // const [value, setValue] = useState('')
    const [messages, loading] = useCollectionData(
      firebase.firestore().collection('messages').orderBy('createdAt')
    )

    const sendMessage = async () => {
        firebase.firestore().collection('messages').add({
            uid: auth.currentUser.uid,
            name: auth.currentUser.displayName,
            // photoURL: user.photoURL,
            data: formatDate(new Date()),
            text: text,
            createdAt: firebase.firestore.FieldValue.serverTimestamp()
        });
        document.querySelector('.msger-input').value = '';
    }

    const formatDate = (date) => {
        const h = "0" + date.getHours();
        const m = "0" + date.getMinutes();

        return `${h.slice(-2)}:${m.slice(-2)}`;
    }

    const writeEmailAndPassword = (event) => {
      text = event.target.value;
    }

    if (loading) {
        return <div>123</div>
    }
    return (
      <section className="msger">
        <header className="msger-header">
          <div className="msger-header-title">
            <i className="fas fa-comment-alt"></i> SimpleChat
          </div>
          <div className="msger-header-options">
            <span><i className="fas fa-cog"></i></span>
          </div>
        </header>
      
        <div className="msger-chat">
          
          {messages.map(message =>
            <div className="msg" style={
              auth.currentUser.uid === message.uid ? {flexDirection: 'row-reverse'} : {}
            }>
              <div className="msg-bubble" style={
                auth.currentUser.uid === message.uid ? {background: '#579ffb' , color: '#fff', borderBottomLeftRadius: 0} :{borderBottomLeftRadius: 0}
              }>
                <div className="msg-info">
                  <div className="msg-info-name" key="">{message.name}</div>
                  <div className="msg-info-time">{message.data}</div>
                </div>
                <div className="msg-text" key="">{message.text}</div>
              </div>
            </div>
          )}
        </div>
      
        <div className="msger-inputarea">
          <input type="text" className="msger-input" placeholder="Enter your message..." onInput={writeEmailAndPassword}/>
          <button type="submit" className="msger-send-btn" onClick={sendMessage}>Send</button>
        </div>
      </section>
    );
};

export default Chat;